/* Tabs On Bottom - begin
========================================================================== */
.woocommerce.single-product.qode-product-single-tabs-on-bottom {

	.q_tabs {
		clear: both;
		padding: 50px 16.5% 60px;

		#tab-description{
			text-align: center;
		}

		.shop_attributes{
			width: 100%;

			th,td{
				text-align: center;
			}

			tr,td{
				border: 1px solid #e5e5e5;
			}
		}
	}

	.product {

		.single-onsale {
			left: 133px;
		}

		.images {
			width: 63.85%;
			margin-right: 2.3%;
			
			@media only screen and (min-width: 601px) {
				
				figure {
					width: 100%;
					margin: 0;
				}
				
				.woocommerce-product-gallery__image {
					
					&:first-child {
						padding: 0 0 0 114px;
					}
				}
				
				&.woocommerce-product-gallery--with-images {
					
					.woocommerce-product-gallery__image {
						
						&:not(:first-child) {
							position: absolute;
							top: 0;
							left: 0;
							width: 111px;
							height: 111px;
							padding: 0;
							
							a {
								height: 100%;
								
								img {
									height: 100%;
								}
							}
						}
						
						$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
						@for $i from 0 to length($thumbnail_numbers) {
							$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
							
							&:nth-child(#{$thumbnail_number_value}#{n}) {
								$topPos: (111 * $i + 3 * $i)#{px};
								
								top: $topPos;
							}
						}
					}
				}
			}
		}

		&.qode-product-with-gallery {

			.thumbnails {
				width: 111px;
				margin-right: 3px;
				float: left;
				margin-top: 0;

				a {
					width: 100%;
					margin-right: 0;
					margin-bottom: 3px;

					img {
						vertical-align: middle;
					}
				}
			}

			.woocommerce-main-image {

				float: right;
				width: calc(100% - 114px);
			}
		}

		.summary {
			width: 33.85%;

			.qode-sp-separator.separator {
				margin-top: 25px;
				margin-bottom: 25px;
				width: 46px;
			}

			.product-categories{
				margin-bottom: 11px;
			}
		}

		.product_meta {

			border-top: 1px solid #e5e5e5;
			border-bottom: 1px solid #e5e5e5;
			padding: 16px 0px;
			text-align: center;

			> span {
				display: inline-block;
				font-weight: inherit;
				font-size: inherit;
				text-transform: inherit;
				color: inherit;
				border-right: 1px solid #e5e5e5;
				padding-right: 15px;
				padding-left: 12px;

				&:last-child {
					border-right: none;
				}
			}
		}

		.related {
			padding-top: 55px;
		}
	}

}


/* Tabs On Bottom - end
========================================================================== */